<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>服务详情</p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <md-tab-bar
                        v-model="current"
                        :items="items"
                        :has-ink="false"
                        @change="onTabChange"
                ></md-tab-bar>
                <md-swiper
                        ref="swiper"
                        :autoplay="0"
                        :is-prevent="false"
                        :is-loop="false"
                        :has-dots="false"
                        :has-ink="false"
                        @before-change="onSwiperChange">
                    <md-swiper-item>
                        <div class="con-con">
                            <div class="other-content" :style="{height: Height}">
                                <div class="one">
                                    <div class="title">主要成果（学术成就/研究成果/管理成果等）</div>
                                    <div>
                                        <md-cell-item title="设备名称" :addon="editInfo.name"/>
                                        <md-cell-item title="设备简介" :addon="editInfo.intro" />
                                        <md-cell-item title="所属领域" :addon="editInfo.genre"/>
                                        <md-cell-item title="规格型号" :addon="editInfo.specifications" />
                                        <md-cell-item title="仪器所属类别" :addon="editInfo.instrumentsType"/>
                                        <md-cell-item title="设备来源" :addon="editInfo.source"/>
                                        <md-cell-item title="设备厂商"  :addon="editInfo.manufacturer"/>
                                        <md-cell-item title="性能参数"  :addon="editInfo.performanceParameter"/>
                                        <md-cell-item title="主要功能"  :addon="editInfo.majorFunction" />
                                        <md-cell-item title="技术指标"  :addon="editInfo.technicalIndicators"/>
                                    </div>
                                </div>
                                <div class="one">
                                    <md-cell-item title="设备图片" no-border>
                                        <img style="width: 130px;height: 75px" slot="right" :src="editInfo.img" alt="">
                                    </md-cell-item>
                                    <div class="title">其他信息</div>
                                    <div>
                                        <md-cell-item title="联系人" :addon="editInfo.contacts"/>
                                        <md-cell-item title="联系电话" :addon="editInfo.phone" />
                                        <md-cell-item title="联系邮箱" :addon="editInfo.email" />
                                        <md-cell-item title="所在地区" :addon="editInfo.province"/>
                                        <md-cell-item title="通讯地址" :addon="editInfo.address"/>
                                        <md-cell-item title="邮政编码" :addon="editInfo.postalCode"/>
                                        <md-cell-item title="所在单位" :addon="editInfo.unit"/>
                                        <md-cell-item title="单位网址" :addon="editInfo.url"/>
                                        <md-cell-item title="使用事项说明" :addon="editInfo.instructions"/>
                                    </div>
                                </div>
                                <div class="enclosure-con">
                                    <div class="title">附件</div>
                                    <div class="des">{{ editInfo.fileName }}</div>
                                </div>
                            </div>
                        </div>
                    </md-swiper-item>
                    <md-swiper-item>
                        <div class="none" v-if="!applyList.length"  :style="{height: Height}">暂无数据</div>
                        <div class="model" v-if="applyList.length"  v-for="i in applyList">
                            <div class="model-title">
                                <span class="logo"></span>
                                <span class="desc">{{ i.equipmentName }}</span>
                            </div>
                            <div class="model-con">
                                <div>预约人：{{ i.dockingUser }}</div>
                                <div>预约时间：{{ i.createTime }}</div>
                            </div>
                            <div style="float: left;margin-top: 20px;margin-left: 20px;color: #021955" @click="checkDetail(i.id)">查看对接详情</div>
                            <div class="model-foot" v-if="i.status === 0">
                                <md-button class="left" type="default" size="small" @click="deArgee(i.id)" inline>不同意预约</md-button>
                                <md-button class="right"  type="default" size="small" @click="argee(i.id)" inline>同意预约</md-button>
                            </div>
                            <div class="model-foot" v-else>
                                <span class="timeSended">预约时间已发送</span>
                            </div>
                        </div>
                    </md-swiper-item>
                    <md-swiper-item>
                        <div class="none" v-if="!nowList.length"  :style="{height: Height}">暂无数据</div>
                        <div class="model" v-if="nowList.length"  v-for="i in nowList">
                            <div class="model-title">
                                <span class="logo"></span>
                                <span class="desc">{{ i.equipmentName }}</span>
                            </div>
                            <div class="model-con">
                                <div>预约人：{{ i.dockingUser }}</div>
                                <div>预约时间：{{ i.createTime }}</div>
                            </div>
                            <div class="model-foot">
                                <md-button class="left" type="default" size="small" @click="reComplete(i.id)" inline>未完成预约</md-button>
                                <md-button class="right" type="default" size="small" @click="complete(i.id)" inline>完成预约</md-button>
                            </div>
                        </div>
                    </md-swiper-item>
                    <md-swiper-item>
                        <div class="Record">
                            <div class="none" v-if="!serverList.length"  :style="{height: Height}">暂无数据</div>
                            <div class="one" v-if="serverList.length"  v-for="i in serverList">
                                <div class="title">主要成果（学术成就/研究成果/管理成果等）</div>
                                <div class="des">
                                    <div>服务对象：{{ i.equipmentName }}</div>
                                    <div>服务时间：{{ i.createTime }}</div>
                                </div>
                            </div>
                        </div>
                    </md-swiper-item>
                </md-swiper>
                <md-dialog
                        :closable="true"
                        v-model="iconDialog.open"
                        :btns="iconDialog.btns"
                >
                    <img src="../../../assets/personal/service_order_ikon@2x.png" alt="">
                    <p>预约人失信记录已提交系统</p>
                </md-dialog>
            </div>
        </section>
    </main>
</template>

<script>
import { TabBar, Swiper, SwiperItem, Dialog, ScrollView, Button, Icon, CellItem, Toast, ScrollViewMore } from 'mand-mobile'
import MdSwiperItem from 'mand-mobile/components/swiper/swiper-item'
import { format } from '../../../utils/public'
import { getDetail, getDetail1, sendDate } from '../../../apiUrl/personal/server'

export default {
  name: 'News',
  components: {
    MdSwiperItem,
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [CellItem.name]: CellItem,
    [Button.name]: Button,
    [ScrollView.name]: ScrollView,
    [Icon.name]: Icon,
      [Dialog.name]: Dialog,
    [ScrollViewMore.name]: ScrollViewMore
  },
  data () {
    return {
      // 当前选中tab
      current: 0,
      // tab项
      items: [{ name: 0, label: '设备信息' }, { name: 1, label: '预约申请' }, { name: 2, label: '当前预约' }, { name: 3, label: '服务记录' }],
      listLeft: 10,
      isFinished: false,
      Height: '900px',
      listRight: 10,
      applyList: [],
      nowList: [],
      serverList: [],
      equipment: '',
      name: '',
      editInfo: {
        name: '123',
        intro: 'da',
        genre: 'da',
        specifications: 'ds',
        instrumentsType: 'dsa',
        source: 'dsad',
        manufacturer: 'dsgf',
        performanceParameter: 'gf',
        majorFunction: 'jhf',
        technicalIndicators: 'tretre',
        img: 'http://39.98.186.243/junminUserFiles/file/2019-08-14/ceebee84-63ae-43a6-9c06-eff547577567.png',
        contacts: 'dsadsa',
        phone: 'das',
        email: 'dd',
        province: 'dsa',
        city: 'dsa',
        area: 'dass',
        address: 'dsa',
        postalCode: 'dsa',
        unit: 'dsadsa',
        url: 'dsa',
        instructions: 'dsad',
        file: {
          fileName: 'sdasd'
        },
        reviews: 'dsa',
        dockingCount: 'das',
        isCollection: 'dsads'
      },
      appointment: {

      },
        iconDialog: {
            open: false
        }
    }
  },
  mounted () {
    this.getDetail()
    this.getDetail1()
    console.log(window.innerHeight)
    this.Height = window.innerHeight - 128 + 'px'
  },
  methods: {
    getDetail () {
      const args = {
        id: this.$route.query.id
      }
      getDetail(args).then((res) => {
        this.editInfo = res.data
      })
    },
      checkDetail(id) {
        this.$router.push({ path: '/submission/see', query: { id: id } })
      },
    getDetail1 () {
      const args = {
        id: this.$route.query.id,
        status: 1
      }
      getDetail1(args).then((res) => {
        this.applyList = res.data
      })
      const args1 = {
        id: this.$route.query.id,
        status: 2
      }
      getDetail1(args1).then((res) => {
        this.nowList = res.data
      })
      const args2 = {
        id: this.$route.query.id,
        status: 3
      }
      getDetail1(args2).then((res) => {
        this.serverList = res.data
      })
    },
    argee (data) {
      console.log(data)
      this.$router.push({
        path: '/personal/date',
        query: { id: data }
      })
    },
      complete (id) {
          const args = {
              id: id,
              status: 5
          }
          sendDate(args).then((res) => {
              Toast.succeed('操作成功')
          })
      },
      reComplete () {
          const args = {
              id: id,
              status: 6
          }
          sendDate(args).then((res) => {
              this.iconDialog.open = true
          })
      },
    deArgee (id) {
      const args = {
        id: id,
        status: 2
      }
      sendDate(args).then((res) => {
        Toast.succeed('不同意预约提交成功')
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.$refs.swiper.goto(index)
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push('/news/detail')
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      // async data
      setTimeout(() => {
        this.listLeft += 10
        if (this.listLeft >= 15) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }, 1000)
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    addItems () {
      this.list += 5
      // 如果把autoReflow设置为false, 需调用reflowScroller
      this.$refs.scrollView.reflowScroller()
    },
    // 回退
    goBack () {
      this.$router.push('/personal/server')
    }
  }
}
</script>

