                    .right
                        background #021955
                        color #fff
                        border-radius:8px;
            .Record
                margin 0 auto
                border-radius:12px;
                padding 50px 30px
                background:rgba(255,255,255,1);
                box-shadow:0px 4px 24px 0px rgba(2,25,85,0.1);
                .one
                    margin-bottom 30px
                    .title
                        text-align left
