                    .des
                        text-align left
                        padding 0 20px
                        background rgba(153,153,153,.1)
                        font-size 24px
                        margin-top 20px
                        line-height 76px
                        border-radius:8px
                        color #999999
    .md-example-child-scroll-view-0
        height 600px
        background #FFF
        .scroll-view-item
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 174px
    .scroll-view-container
        div
            display flex
            justify-content space-between
            img
                width 260px
            .article
                width 410px
