import request from '../../utils/request'
import apiPath from '../url'
import qs from 'qs'
// 查询用户信息
export function getList (data) {
  data = qs.stringify(data)
  console.log(data)
  return request({
    url: apiPath.personal.server.getList,
    method: 'post',
    data: data
  })
}
